<template>
  <div class="personal">
    <div class="container">
      <h1>{{ $t('簡易報告') }}</h1>
      <div class="performance">
        <p>{{ $t('作答者：') }}<span style="font-weight: 900"> {{ answerGroup.user.name }} </span></p>
        <table>
          <tr>
            <th>{{ $t('項目') }}</th>
            <th>{{ $t('個人表現') }}</th>
            <th>{{ $t('全體平均表現') }}</th>
          </tr>
          <tr>
            <td>{{ $t('分數') }}</td>
            <td>
              {{ dataInfo.scoreGot }} / {{ dataInfo.totalScore }} {{ $t('分') }}
            </td>
            <td>
              {{ dataInfo.overallScoreAvg }} / {{ dataInfo.totalScore }} {{ $t('分') }}
            </td>
          </tr>
          <tr>
            <td>{{ $t('平均每題花費時間') }}</td>
            <td>
              {{ dataInfo.averageTimeSpent }} {{ $t('秒') }}
            </td>
            <td>
              {{ dataInfo.averageOverallTimeSpend }} {{ $t('秒') }}
            </td>
          </tr><tr>
            <td>{{ $t('總花費時間') }}</td>
            <td>
              {{ $tc('n秒', dataInfo.timeSpent, {n: dataInfo.timeSpent}) }}
            </td>
            <td>
              {{ $tc('n秒', dataInfo.overallTimeSpend, {n: dataInfo.overallTimeSpend}) }}
            </td>
          </tr>
        </table>
      </div>

      <div class="style-content">
        <h1>{{ $t('詳細資訊') }}</h1>
        <div class="result-area">
          <div
            v-for="(item, key) in answerGroup.answerList"
            :key="key"
            class="group-btn"
            :class="{'active': displayIndex === key}"
            @click="selectOne(key)"
          >
            <div class="number">
              {{ key+1 }}
            </div>
            <div class="result">
              <i v-if="!item.userAnswerSet" class="fas fa-question no-answer" />
              <i v-if="item.sourceQuiz.answerSet.length === 0" class="fas fa-minus no-answer" />

              <i v-if="answerResult(item)" class="fas fa-check correct" />
              <i v-if="!answerResult(item)" class="fas fa-times incorrect" />
            </div>
          </div>
        </div>

        <!-- {{ overallQuizData }} -->

        <div
          v-for="(item, index) in answerGroup.answerList"
          :key="index"
          class="detail-area"
        >
          <transition name="fade">
            <div v-if="displayIndex === index" class="detail">
              <div class="mb-4 text-center">
                {{ $t('第n題', {n: index+1}) }}
              </div>
              <div class="option">
                <audio
                  v-if="item.sourceQuiz.enableAudio && item.sourceQuiz.audioUrl"
                  controls
                  controlsList="nodownload"
                >
                  <source :src="item.sourceQuiz.audioUrl" type="audio/mp4">
                </audio>
                <img
                  class="option-img"
                  :src="item.sourceQuiz.previewImage.url+'/240'"
                >
              </div>
              <table>
                <tr>
                  <td>{{ $t('題型') }}</td>
                  <td v-if="item.sourceQuiz.answerType === 'single' && !item.sourceQuiz.multipleSelect">
                    {{ $t('單選題') }}
                  </td>
                  <td v-if="item.sourceQuiz.multipleSelect">
                    {{ $t('複選題') }}
                  </td>
                  <td v-if="!item.sourceQuiz.answerType && !item.sourceQuiz.multipleSelect">
                    {{ $t('無設定答案') }}
                  </td>
                </tr>
                <!-- <tr v-if="item.sourceQuiz.answerType">
                  <td>{{ $t('answerType') }}</td>
                  <td>{{ item.sourceQuiz.answerType }}</td>
                </tr> -->
                <tr>
                  <td>{{ $t('我的答案') }}</td>
                  <td>
                    <span v-for="(answer, i) in item.userAnswerSet" :key="i"
                          :class="{
                            'no-answer': !item.userAnswerSet || item.userAnswerSet.length === 0 || !item.sourceQuiz.answerSet,
                            correct: item.userAnswerSet && item.userAnswerSet.length > 0 && item.sourceQuiz.answerSet && answerResult(item),
                            incorrect: item.userAnswerSet && item.userAnswerSet.length > 0 && item.sourceQuiz.answerSet && !answerResult(item)} "
                    >
                      {{ answer }}
                    </span>
                  </td>
                </tr>
                <tr>
                  <td>{{ $t('本題的答案') }}</td>
                  <td v-if="item.sourceQuiz.answer">
                    {{ item.sourceQuiz.answer }}
                  </td>
                  <td>
                    <span v-for="(answer, i) in item.sourceQuiz.answerSet" :key="i">
                      {{ answer }}
                    </span>
                  </td>
                </tr>
                <tr>
                  <td>{{ $t('我花的時間') }}</td>
                  <td>{{ $tc('n秒', item.timeSpent, {n: item.timeSpent}) }}</td>
                </tr>
                <tr v-if="overallQuizData[index]">
                  <td>{{ $t('平均花費時間') }}</td>
                  <td>{{ getTimeSpent(index) }} {{ $t('秒') }}</td>
                </tr>
                <tr v-if="overallQuizData[index]">
                  <td>{{ $t('平均得分') }}</td>
                  <td>{{ getSocreAvg(index) }}</td>
                </tr>
              </table>
            </div>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Utils from '@/libs/Utility';

export default {
  props: {
    answerGroup: {
      type: Object,
      required: true,
    },
    overallQuizData: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      displayIndex: 0,
    };
  },
  computed: {
    dataInfo() {
      const dataInfo = {
        dataLength: this.answerGroup.sourceQuizGroupSize,
        totalScore: this.answerGroup.totalScore,
        scoreGot: 0,
        timeSpent: 0,
        averageTimeSpent: 0,
        overallScoreAvg: 0,
        overallTimeSpend: 0,
        averageOverallTimeSpend: 0,
      };
      dataInfo.scoreGot = Utils.formatFloat(this.answerGroup.scoreGot);
      dataInfo.timeSpent = Utils.formatFloat(this.answerGroup.timeSpent);
      dataInfo.averageTimeSpent = Utils.formatFloat(dataInfo.timeSpent / dataInfo.dataLength);

      this.overallQuizData.forEach((data) => {
        dataInfo.overallScoreAvg += data.scoreAvg;
        dataInfo.overallTimeSpend += data.timeSpent;
      });
      dataInfo.overallScoreAvg = parseInt(dataInfo.overallScoreAvg, 10);
      dataInfo.overallTimeSpend = Utils.formatFloat(dataInfo.overallTimeSpend / dataInfo.dataLength);
      dataInfo.averageOverallTimeSpend = Utils.formatFloat(dataInfo.overallTimeSpend / dataInfo.dataLength);

      return dataInfo;
    },
  },
  created() {
    console.log(this.answerGroup);
  },
  methods: {
    answerResult(item) {
      let result = false;
      const userAnswerList = item.userAnswerSet;
      const correctAnswerList = item.sourceQuiz.answerSet;
      for (let index = 0; index < correctAnswerList.length; index += 1) {
        if (correctAnswerList.length === userAnswerList.length) {
          if (correctAnswerList[index] === userAnswerList[index]) {
            result = true;
          } else {
            result = false;
            break; // 多選題只要有一個錯就會錯
          }
        } else {
          result = false; // 答案數量不同就是錯
        }
      }
      return result;
    },
    selectOne(key) {
      this.displayIndex = key;
    },
    getTimeSpent(index) {
      let time = 0;
      time = Utils.formatFloat(this.overallQuizData[index].timeSpent);
      return time;
    },
    getSocreAvg(index) {
      let scoreAvg = 0;
      console.log('scoreAvg:', this.overallQuizData[index].scoreAvg);
      scoreAvg = this.overallQuizData[index].scoreAvg;
      // scoreAvg = Utils.formatFloat(this.overallQuizData[index].scoreAvg * 100);
      return scoreAvg;
    },
  },
};
</script>

<style scoped lang="scss">
.personal {
  .container{
    max-width: 1200px;
    padding: 0 20px;
    margin: auto;
    h1{
      display: flex;
      justify-content: flex-start;
      border-bottom: 2px solid black;
      padding-bottom: 10px;
      margin-top: 20px;
      margin-bottom: 30px;
      font-size: 1.8rem;
    }
    .performance{
      p{
        max-width: 500px;
        margin: auto;
      }
      table{
        width: 100%;
        max-width: 500px;
        margin: auto;
        text-align: center;
      }
      td{
        padding: 10px;
      }
      table, td, th{
        border: 1px solid #777;
        border-collapse: collapse;
      }
    }
    .result-area{
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      height: 100%;
      max-height: 85px;
      overflow: auto;
      margin-bottom: 20px;
      .group-btn{
        display: flex;
        justify-content: space-around;
        align-items: center;
        border: 1px solid lightblue;
        width: 14%;
        padding: 5px;
        margin: 0 10px;
        margin-bottom: 5px;
        .number{
          width: 10px;
          margin-right: auto;
        }
        .result {
          margin-right: auto;
          span{
          i{
            font-size: 20px;
          }
          }
        }
        &:hover {
          cursor: pointer;
          background-color: rgba(173, 216, 230, 0.5);
        }
      }
      .active {
        background-color: rgba(173, 216, 230, 0.5);
      }
    }
    .detail-area{
      margin-bottom: 20px;
      .detail{
        border: 1px solid lightblue;
        padding: 20px;
        max-width: 300px;
        margin: auto;

        audio {
          width: 100%
        }

        img {
          width: 100%;
        }
        table{
          width: 100%;
          margin-top: 20px;
          text-align: left;
        }
        td{
          padding: 10px;
        }
        table, td{
          border: 1px solid black;
          border-collapse: collapse;
        }
      }
    }

    .no-answer {
      color: gray;
      font-weight: 900;
    }

    .correct {
      color: green;
      font-weight: 900;
    }
    .incorrect {
      color: red;
      font-weight: 900;
    }

    .fade-enter-active, .fade-leave-active {
      transition: opacity 1s;
    }
    .fade-enter, .fade-leave-to {
      transition: opacity 0.2s;
      opacity: 0;
    }
  }
}

@media screen and (min-width: 700px) {
  .personal {
    .container{
      // .only-style-change{
      //   display: block;
      // }
      .style-content{
        h1{
          // display: none;
          margin-bottom: 20px;
        }
        .result-area{
          float: left;
          display: block;
          height: 637px;
          max-height: 100%;
          overflow: auto;
          .group-btn{
            margin-top: 5px;
            width: 150px;
          }
        }
        // .detail-area{
        //   // margin-left: 170px;
        // }
      }
    }
  }
}

</style>
